/* Navigation Styles */
.navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 1rem 0;
  z-index: 1000;
}

.nav-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}

.nav-logo {
  display: flex;
  align-items: center;
}

.logo-image {
  height: 60px; /* Reduced from 100px for better mobile display */
  width: auto;
  transition: opacity 0.3s ease;
}

.logo-image:hover {
  opacity: 0.8;
}

.nav-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 2rem;
}

.nav-list li a {
  color: #7070FF;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  transition: color 0.3s ease;
  padding: 0.5rem 0; /* Added padding for better touch targets */
}

.nav-list li a:hover {
  color: #ffffff;
}

/* Existing Portfolio Styles */
.portfolio-container {
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden;
  padding-top: 80px;
  min-height: 100vh;
  background-color: #000000;
  color: #ffffff;
}

.portfolio-container::before,
.portfolio-container::after {
  content: '';
  position: fixed;
  width: 600px;
  height: 600px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(112, 112, 255, 0.15) 0%, transparent 70%);
  filter: blur(60px);
  z-index: -1;
  pointer-events: none;
}

.portfolio-container::before {
  top: -200px;
  left: -200px;
}

.portfolio-container::after {
  bottom: -200px;
  right: -200px;
  background: radial-gradient(circle, rgba(112, 112, 255, 0.1) 0%, transparent 70%);
}

/* Typewriter Effect Styles */
.typewriter-text {
  font-size: 3.5rem;
  color: #7070FF;
  margin-bottom: 1rem;
  font-weight: 600;
  min-height: 4.5rem; /* Prevent layout shift */
  display: flex;
  align-items: center;
  justify-content: center;
}

.typewriter-cursor {
  display: inline-block;
  background-color: #7070FF;
  margin-left: 3px;
  width: 3px;
  animation: blink 1s infinite;
}

@keyframes blink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0; }
}

/* Mobile responsive for typewriter */
@media (max-width: 480px) {
  .typewriter-text {
    font-size: 2rem;
    min-height: 3rem;
  }
}

.hero-section {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #000;
  padding: 2rem;
}

.name {
  font-size: 3.5rem;
  color: #7070FF;
  margin-bottom: 1rem;
  font-weight: 600;
}

.title {
  font-size: 2rem;
  color: #fff;
  opacity: 0.9;
}

/* Add a subtle fade-in animation for sections */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* About Section Styles */
.about-section,
.skills-section,
.education-section,
.projects-section,
.certifications-section,
.contact-section {
  padding: 6rem 2rem;
  max-width: 1200px;
  width: calc(100% - 4rem); /* Adjusted to account for padding */
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box; /* Add this to include padding in width calculation */
}

.section-title {
  font-size: 2.5rem;
  color: #7070FF;
  text-align: center;
  margin-bottom: 3rem;
  font-weight: 600;
}

.about-content {
  display: flex;
  gap: 4rem;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 1000px;
}

.profile-image-container {
  flex: 0 0 auto;
  width: 320px;
  height: 320px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(112, 112, 255, 0.3);
  border: 3px solid rgba(112, 112, 255, 0.2);
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  transition: transform 0.3s ease;
}

.profile-image-container:hover .profile-image {
  transform: scale(1.05);
}

.about-text-container {
  flex: 1;
  max-width: 600px;
  padding: 0 1rem;
}

.about-text {
  color: #ffffff;
  font-size: 1.1rem;
  line-height: 1.8;
  text-align: justify;
  margin: 0 0 2rem 0;
}

.social-links {
  display: flex;
  gap: 1.5rem;
  margin-top: 1.5rem;
  flex-wrap: wrap; /* Allow wrapping on small screens */
}

.social-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #7070FF;
  text-decoration: none;
  font-size: 1rem;
  transition: all 0.3s ease;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  background-color: rgba(112, 112, 255, 0.1);
}

.social-link:hover {
  color: white;
  background-color: #7070FF;
  transform: translateY(-2px);
}

.social-icon {
  font-size: 1.2rem;
}

.cv-link {
  background-color: rgba(112, 112, 255, 0.15);
  border: 1px solid rgba(112, 112, 255, 0.3);
}

.cv-link:hover {
  background-color: #7070FF;
  border-color: #7070FF;
}


/* Skills Section Container */
.skills-section {
  padding: 6rem 2rem;
  max-width: 1200px;
  width: calc(100% - 4rem);
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
}

/* Main Skills Container */
.skills-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  justify-content: center; /* CENTER the skill items */
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 100%;
}

/* Individual Skill Item */
.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center horizontally */
  justify-content: center; /* Center vertically */
  gap: 0.8rem;
  padding: 1.5rem;
  background-color: rgba(0, 0, 0, 0.4); /* Darker background */
  backdrop-filter: blur(5px);
  border-radius: 12px;
  transition: all 0.3s ease;
  width: 120px;
  min-height: 120px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
}

.skill-item:hover {
  transform: translateY(-8px);
  background-color: rgba(112, 112, 255, 0.15);
  box-shadow: 0 8px 25px rgba(112, 112, 255, 0.3);
  border-color: rgba(112, 112, 255, 0.4);
}

/* Skill Icons */
.skill-icon {
  font-size: 3rem;
  transition: all 0.3s ease;
  color: #7070FF;
  filter: drop-shadow(0 2px 4px rgba(112, 112, 255, 0.3));
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: center;
}

.skill-item:hover .skill-icon {
  transform: scale(1.1);
  color: #ffffff;
  filter: drop-shadow(0 4px 8px rgba(255, 255, 255, 0.5));
}

/* Text-based icons (C# and SQL)*/
.skill-icon-text {
  font-size: 2.2rem;
  font-weight: bold;
  color: #7070FF;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 3rem;
  width: 100%;
  text-align: center;
  transition: all 0.3s ease;
  filter: drop-shadow(0 2px 4px rgba(112, 112, 255, 0.3));
}

.skill-item:hover .skill-icon-text {
  color: #ffffff;
  transform: scale(1.1);
  filter: drop-shadow(0 4px 8px rgba(255, 255, 255, 0.5));
}

/* Skill Names */
.skill-name {
  color: #ffffff;
  font-size: 0.95rem;
  font-weight: 500;
  text-align: center;
  transition: all 0.3s ease;
  letter-spacing: 0.5px;
  width: 100%;
}

.skill-item:hover .skill-name {
  color: #7070FF;
  font-weight: 600;
}

/* Skill Category Containers  */
.skill-category {
  margin-bottom: 3rem;
  width: 100%;
  padding: 2rem; 
  background: transparent; 
  border-radius: 0; 
  border: none; 
  transition: none; 
  position: relative;
  overflow: visible; 
}

.skill-category::before {
  display: none; 
}

.skill-category:hover {
  background: transparent; /* Keep transparent */
  border: none; /* No borders */
  transform: none; /* No transform */
  box-shadow: none; /* No shadow */
}

/* Category Titles  */
.skill-category-title {
  font-size: 1.8rem;
  color: #7070FF;
  text-align: left;
  margin-bottom: 2rem;
  font-weight: 600;
  padding-left: 1rem;
  border-left: 4px solid #7070FF; /* Keep only left border */
  position: relative;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.skill-category-title::after {
  display: none; /* REMOVE bottom accent line */
}

.skill-category:nth-child(1),
.skill-category:nth-child(2),
.skill-category:nth-child(3),
.skill-category:nth-child(4),
.skill-category:nth-child(5) {
  border: none; 
}

/* RESPONSIVE DESIGN */

/* Tablet and smaller screens */
@media (max-width: 768px) {
  .skills-section {
    padding: 4rem 1.5rem;
    min-height: auto;
  }

  .skill-category-title {
    font-size: 1.5rem;
    text-align: center;
    border-left: none;
    border-bottom: 3px solid #7070FF;
    padding-left: 0;
    padding-bottom: 0.8rem;
    margin-bottom: 1.5rem;
  }
  
  .skill-category .skills-container {
    justify-content: center; /* Keep centered on tablet */
    gap: 1.2rem;
  }
  
  .skill-category {
    padding: 1.5rem 1rem; /* Reduced padding */
    margin-bottom: 2.5rem;
  }

  .skill-item {
    width: calc(33.333% - 1rem);
    min-width: 100px;
    padding: 1.2rem;
  }

  .skill-icon {
    font-size: 2.5rem;
  }

  .skill-icon-text {
    font-size: 1.8rem;
  }

  .skill-name {
    font-size: 0.85rem;
  }
}

/* Mobile devices */
@media (max-width: 480px) {
  .skills-section {
    padding: 3rem 1rem;
  }

  .skill-category-title {
    font-size: 1.3rem;
    margin-bottom: 1.2rem;
  }
  
  .skill-category {
    padding: 1rem; /* Minimal padding */
    margin-bottom: 2rem;
  }
  
  .skill-category .skills-container {
    gap: 1rem;
    justify-content: center; /* Keep centered on mobile */
  }

  .skill-item {
    width: calc(50% - 0.5rem);
    min-width: 90px;
    padding: 1rem;
    min-height: 100px;
  }

  .skill-icon {
    font-size: 2.2rem;
  }

  .skill-icon-text {
    font-size: 1.6rem;
  }

  .skill-name {
    font-size: 0.8rem;
  }
}

/* Very small devices */
@media (max-width: 360px) {
  .skill-item {
    width: 100%;
    margin-bottom: 1rem;
    min-height: 90px;
  }

  .skill-category-title {
    font-size: 1.2rem;
  }

  .skill-icon {
    font-size: 2rem;
  }

  .skill-icon-text {
    font-size: 1.4rem;
  }

  .skill-name {
    font-size: 0.75rem;
  }
}

/* Animation for skill items */
.skill-item {
  opacity: 0;
  transform: translateY(30px);
  animation: skillFadeIn 0.6s ease-out forwards;
}

@keyframes skillFadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Staggered animation delays */
.skill-item:nth-child(1) { animation-delay: 0.1s; }
.skill-item:nth-child(2) { animation-delay: 0.2s; }
.skill-item:nth-child(3) { animation-delay: 0.3s; }
.skill-item:nth-child(4) { animation-delay: 0.4s; }
.skill-item:nth-child(5) { animation-delay: 0.5s; }

/* Category title animations */
.skill-category-title {
  opacity: 0;
  transform: translateX(-30px);
  animation: titleSlideIn 0.8s ease-out forwards;
}

@keyframes titleSlideIn {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Enhanced hover effect without weird frames */
.skill-item:hover {
  box-shadow: 0 8px 25px rgba(112, 112, 255, 0.3);
}

/* Education Section Styles */
.education-container {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  max-width: 900px;
  margin: 0 auto;
  width: 100%;
}

.education-item {
  display: flex;
  align-items: center;
  gap: 2.5rem;
  padding: 2rem;
  background-color: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.education-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 12px rgba(112, 112, 255, 0.2);
}

.education-logo-container {
  flex-shrink: 0;
  width: 150px;
  height: 150px;
  border-radius: 10px;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
}

.education-logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.education-details {
  flex: 1;
}

.education-title {
  color: #7070FF;
  font-size: 1.6rem;
  margin: 0 0 0.8rem 0;
}

.education-school {
  color: #ffffff;
  font-size: 1.4rem;
  margin: 0 0 0.8rem 0;
}

.education-period {
  color: #cccccc;
  font-size: 1.2rem;
  margin: 0;
}

/* ===== CERTIFICATIONS SECTION - SIDE BY SIDE LAYOUT ===== */

/* Certifications Section Styles */
.certifications-section {
  padding: 6rem 2rem;
  max-width: 1200px;
  width: calc(100% - 4rem);
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
}

.certifications-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 2 columns side by side */
  gap: 2rem;
  max-width: 1000px;
  margin: 0 auto;
  width: 100%;
}

.certification-item {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  height: fit-content;
  display: flex;
  flex-direction: column;
}

.certification-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(112, 112, 255, 0.3);
  border-color: rgba(112, 112, 255, 0.3);
}

.certification-title {
  color: #7070FF;
  font-size: 1.4rem;
  margin: 0 0 1rem 0;
  font-weight: 600;
  line-height: 1.3;
}

.certification-issuer {
  color: #ffffff;
  font-size: 1.1rem;
  margin: 0 0 0.5rem 0;
  font-weight: 500;
}

.certification-date {
  color: #cccccc;
  font-size: 0.95rem;
  margin: 0 0 1.5rem 0;
}

.certification-description {
  color: #ffffff;
  font-size: 0.95rem;
  line-height: 1.6;
  margin: 0 0 1.5rem 0;
  flex: 1;
}

.certification-link {
  display: inline-block;
  padding: 0.8rem 1.5rem;
  background-color: rgba(112, 112, 255, 0.1);
  color: #7070FF;
  text-decoration: none;
  border-radius: 8px;
  transition: all 0.3s ease;
  text-align: center;
  font-weight: 500;
  margin-top: auto;
}

.certification-link:hover {
  background-color: rgba(112, 112, 255, 0.2);
  transform: translateY(-2px);
  color: #ffffff;
  box-shadow: 0 4px 12px rgba(112, 112, 255, 0.3);
}

/* RESPONSIVE DESIGN */

/* Large tablets */
@media (max-width: 1024px) {
  .certifications-container {
    max-width: 900px;
    gap: 1.5rem;
  }
  
  .certification-item {
    padding: 1.8rem;
  }
  
  .certification-title {
    font-size: 1.3rem;
  }
}

/* Small tablets */
@media (max-width: 768px) {
  .certifications-section {
    padding: 4rem 1.5rem;
    min-height: auto;
  }
  
  .certifications-container {
    grid-template-columns: 1fr; /* Single column on tablets */
    gap: 1.5rem;
    max-width: 600px;
  }
  
  .certification-item {
    padding: 1.5rem;
  }
  
  .certification-title {
    font-size: 1.2rem;
  }
  
  .certification-issuer {
    font-size: 1rem;
  }
  
  .certification-description {
    font-size: 0.9rem;
  }
}

/* Mobile devices */
@media (max-width: 480px) {
  .certifications-section {
    padding: 3rem 1rem;
  }
  
  .certifications-container {
    gap: 1rem;
    max-width: 100%;
  }
  
  .certification-item {
    padding: 1.2rem;
  }
  
  .certification-title {
    font-size: 1.1rem;
    margin-bottom: 0.8rem;
  }
  
  .certification-issuer {
    font-size: 0.95rem;
  }
  
  .certification-date {
    font-size: 0.85rem;
    margin-bottom: 1rem;
  }
  
  .certification-description {
    font-size: 0.85rem;
    margin-bottom: 1rem;
  }
  
  .certification-link {
    padding: 0.7rem 1.2rem;
    font-size: 0.9rem;
  }
}

/* Very small devices */
@media (max-width: 360px) {
  .certification-item {
    padding: 1rem;
  }
  
  .certification-title {
    font-size: 1rem;
  }
  
  .certification-link {
    width: 100%;
    text-align: center;
  }
}

/* Animation for certification items */
.certification-item {
  opacity: 0;
  transform: translateY(30px);
  animation: certFadeIn 0.6s ease-out forwards;
}

@keyframes certFadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Staggered animation delays */
.certification-item:nth-child(1) { animation-delay: 0.1s; }
.certification-item:nth-child(2) { animation-delay: 0.2s; }
.certification-item:nth-child(3) { animation-delay: 0.3s; }
.certification-item:nth-child(4) { animation-delay: 0.4s; }
/* Projects Section Styles */
.projects-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  max-width: 1000px;
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
}

.project-item {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  display: flex;
  flex-direction: column;
  width: 100%; /* Ensure full width */
  box-sizing: border-box;
  margin-bottom: 1.5rem;
}

.project-item:hover {
  transform: translateY(-5px);
}

.project-image-container {
  width: 100%;
  aspect-ratio: 16/9;
  overflow: hidden;
}

.project-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-content {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  flex: 1;
  width: 100%;
  box-sizing: border-box;
}

.project-title {
  color: #7070FF;
  font-size: 1.4rem;
  margin: 0 0 1rem 0;
  word-wrap: break-word;
  font-weight: 600;
  text-align: left;
}

/* FIX: Modified to ensure consistent project description display */
.project-description {
  color: #ffffff;
  font-size: 1rem;
  line-height: 1.6;
  margin: 0 0 1.5rem 0;
  flex: 1;
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word; /* Added to help with long words */
  hyphens: auto; /* Added to help with long words */
  display: block;
  visibility: visible;
  opacity: 1;
  width: 100%; /* Added to ensure full width */
  max-width: 100%; /* Added to prevent overflow */
}

.project-tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  justify-content: center;
  margin: auto 0;
  padding: 1rem 0;
  background-color: rgba(112, 112, 255, 0.05);
  border-radius: 0 0 10px 10px;
  width: 100%;
  box-sizing: border-box;
}

.tech-item {
  padding: 0.5rem 1rem;
  background-color: rgba(112, 112, 255, 0.1);
  border-radius: 20px;
  transition: all 0.3s ease;
  margin-bottom: 0.5rem;
}

.tech-item:hover {
  background-color: rgba(112, 112, 255, 0.2);
  transform: translateY(-2px);
}

.tech-name {
  color: #7070FF;
  font-size: 0.9rem;
  font-weight: 500;
}

/* Contact Section Styles */
.contact-section {
  padding: 6rem 2rem;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box; /* Ensure padding is included in width calculation */
}

.contact-info {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  justify-content: center;
  margin-bottom: 3rem;
  width: 100%;
  max-width: 800px;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem;
  background-color: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  flex: 1;
  max-width: 300px;
}

.contact-item:hover {
  transform: translateY(-5px);
  background-color: rgba(112, 112, 255, 0.1);
  border-color: rgba(112, 112, 255, 0.3);
}

.contact-icon {
  font-size: 1.8rem;
  color: #7070FF;
  min-width: 2rem;
}

.contact-details {
  flex: 1;
  overflow: hidden;
}

.contact-details h3 {
  color: #7070FF;
  font-size: 1.2rem;
  margin: 0 0 0.5rem 0;
  font-weight: 500;
  white-space: nowrap;
}

.contact-details a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.3s ease;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.contact-form {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  background-color: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-sizing: border-box; /* Add this to include padding in width calculation */
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  width: 100%;
}

.form-group label {
  color: #7070FF;
  font-size: 1.1rem;
  font-weight: 500;
}

.form-group input,
.form-group textarea {
  padding: 1rem;
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  color: #ffffff;
  font-size: 1rem;
  transition: all 0.3s ease;
  width: 100%;
  box-sizing: border-box; /* Add this to include padding in width calculation */
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #7070FF;
  background-color: rgba(112, 112, 255, 0.1);
}

.form-group textarea {
  min-height: 150px;
  resize: vertical;
}

.submit-btn {
  padding: 1rem 2rem;
  background-color: #7070FF;
  color: #ffffff;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  width: fit-content;
  align-self: flex-start;
}

.submit-btn:hover {
  background-color: #5858ff;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(112, 112, 255, 0.3);
}

.project-link {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin: 1rem auto 0;
  color: #7070FF;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
  width: fit-content;
  padding: 0.5rem 1rem;
  background-color: rgba(112, 112, 255, 0.1);
  border-radius: 5px;
}

.project-link:hover {
  color: #ffffff;
  background-color: rgba(112, 112, 255, 0.2);
  transform: translateY(-2px);
}

.link-icon {
  font-size: 1.2rem;
}

/* Ensure images don't overflow */
img {
  max-width: 100%;
  height: auto;
}

/* Add hamburger menu styles */
.nav-toggle {
  display: none;
  font-size: 1.5rem;
  color: #7070FF;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
}

/* Animation Keyframes */
@keyframes slideUp {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Apply animations to text elements only */
.name,
.title,
.section-title,
.about-text,
.education-title,
.education-school,
.education-period,
.project-title,
.project-description,
.tech-name,
.contact-details h3,
.contact-details a {
  opacity: 0;
  transform: translateY(50px);
  animation: slideUp 0.8s ease-out forwards;
}

/* Add animation delays for staggered effect */
.name { animation-delay: 0.1s; }
.title { animation-delay: 0.2s; }
.section-title { animation-delay: 0.3s; }
.about-text { animation-delay: 0.4s; }
.education-title { animation-delay: 0.5s; }
.education-school { animation-delay: 0.6s; }
.education-period { animation-delay: 0.7s; }
.project-title { animation-delay: 0.8s; }
.project-description { animation-delay: 0.9s; }
.tech-name { animation-delay: 1s; }
.contact-details h3 { animation-delay: 1.1s; }
.contact-details a { animation-delay: 1.2s; }

